import React, { Component } from 'react';
import { Card, Row, Col, Input, Button, DatePicker } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { TableData } from 'components/common';
import { dateToString } from 'utils/formatUtil';
import moment from 'moment';
import {
  getLevelUpStaffProfiles,
  confirmLevelUps,
  exportLevelUpNotes,
} from 'services/organization_homepage';
import './style.scss';

class ListEnrollmentPlan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
      note: '',
      dataNoteChange: {},
      nextLevelStartDate: '',
      dataDateChange: {},
    };
  }

  handleExportLevelUpNotes = async (index, record) => {
    const { dataDateChange } = this.state;
    const { dataNoteChange } = this.state;

    if (index in dataDateChange) {
      await this.setState({ nextLevelStartDate: dataDateChange[index].date });
    } else {
      await this.setState({ nextLevelStartDate: record.nextLevelStartDate });
    }

    if (index in dataNoteChange) {
      this.setState({ note: dataNoteChange[index].note });
    } else {
      this.setState({ note: record.note });
    }

    const dataExport = {
      id: record.id,
      currentLevel: record.currentLevel,
      nextLevelStartDate: this.state.nextLevelStartDate,
      note: this.state.note,
    };
    await exportLevelUpNotes(dataExport);
  };

  handleConfirmLevelUps = async (index, record) => {
    const { dataDateChange } = this.state;
    const { dataNoteChange } = this.state;

    if (index in dataDateChange) {
      this.setState({ nextLevelStartDate: dataDateChange[index].date });
    } else {
      this.setState({ nextLevelStartDate: record.nextLevelStartDate });
    }

    if (index in dataNoteChange) {
      this.setState({ note: dataNoteChange[index].note });
    } else {
      this.setState({ note: record.note });
    }

    const dataConfirm = {
      id: record.id,
      currentLevel: record.currentLevel,
      nextLevelStartDate: this.state.nextLevelStartDate,
      note: this.state.note,
      updatedById: record.id,
    };
    await confirmLevelUps(dataConfirm);
    this.setState({ reload: this.state.reload + 1 });
  };

  handleDateChange = async (index, date) => {
    const dataChange = { index, date: dateToString(date._d, 'YYYY/MM/DD') };
    const { dataDateChange } = this.state;
    dataDateChange[index] = dataChange;
    this.setState({ dataDateChange });
  };

  handleInputChange = async (index, note) => {
    const dataChange = { index, note: note.target.value };
    const { dataNoteChange } = this.state;
    dataNoteChange[index] = dataChange;
    this.setState({ dataNoteChange });
  };

  render() {
    const { TextArea } = Input;
    const { reload } = this.state;
    const columns = [
      {
        title: 'Mã số',
        dataIndex: 'staffId',
        align: 'center',
        key: 'staffId',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'fullname',
        width: '150px',
        align: 'center',
        key: 'fullname',
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        align: 'center',
        key: 'dob',
        render: (text, record) => dateToString(record.dob, 'DD/MM/YYYY'),
      },
      {
        title: 'Bậc hiện tại',
        dataIndex: 'currentLevel',
        align: 'center',
        key: 'currentLevel',
      },
      {
        title: 'HS hiện tại',
        dataIndex: 'currentLevelPoint',
        align: 'center',
        key: 'currentLevelPoint',
      },
      {
        title: 'Ngày hưởng',
        dataIndex: 'levelStartDate',
        align: 'center',
        key: 'levelStartDate',
        render: (text, record) => dateToString(record.levelStartDate, 'DD/MM/YYYY'),
      },
      {
        title: 'Bậc kế tiếp',
        dataIndex: 'nextLevel',
        align: 'center',
        key: 'nextLevel',
      },
      {
        title: 'HS tăng',
        dataIndex: 'nextLevelPoint',
        align: 'center',
        key: 'nextLevelPoint',
      },
      {
        title: 'Ngày bắt đầu',
        dataIndex: 'nextLevelStartDate',
        align: 'center',
        key: 'nextLevelStartDate',
        render: (text, record, index) => (
          <DatePicker
            placeholder="Ngày bắt đầu"
            // value={moment(dateToString(record.nextLevelStartDate, 'DD/MM/YYYY'), 'DD/MM/YYYY')}
            defaultValue={moment(
              dateToString(record.nextLevelStartDate, 'DD/MM/YYYY'),
              'DD/MM/YYYY'
            )}
            format="DD/MM/YYYY"
            onChange={date => this.handleDateChange(index, date)}
          />
        ),
      },
      {
        title: 'Ghi chú',
        dataIndex: 'note',
        align: 'center',
        key: 'note',
        render: (text, record, index) => (
          <TextArea
            placeholder="Ghi chú"
            autosize
            onBlur={note => this.handleInputChange(index, note)}
          />
        ),
      },
      {
        title: 'In quyết định',
        dataIndex: 'print',
        align: 'center',
        key: 'print',
        render: (text, record, index) => (
          <Button
            onClick={() => this.handleExportLevelUpNotes(index, record)}
            type="primary"
            icon="printer"
          />
        ),
      },
      {
        title: 'Xác nhận',
        dataIndex: 'confirm',
        align: 'center',
        key: 'confirm',
        render: (text, record, index) => (
          <Button
            onClick={() => this.handleConfirmLevelUps(index, record)}
            // type="primary"
            icon="like"
            style={{ backgroundColor: '#00cc00' }}
            className="btn-confirm"
          />
        ),
      },
    ];
    return (
      <Card title="Danh sách cán bộ tăng lương trong quý">
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getLevelUpStaffProfiles}
              columns={columns}
              rowKey="staffId"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListEnrollmentPlan />
  </Page>
);



