import { request, requestDownload } from 'utils';

// request.prefix = '/api/categories';
const prefix = '/api/organization';

export const getLevelUpStaffProfiles = ({ page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: `/StaffProfile/LevelUpStaffProfile`,
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const confirmLevelUps = ({ id, currentLevel, nextLevelStartDate, note, updatedById }) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/LevelUp`,
    data: {
      id,
      currentLevel,
      nextLevelStartDate,
      note,
      updatedById,
    },
    method: 'PUT',
  });

export const exportLevelUpNotes = ({ id, currentLevel, nextLevelStartDate, note }) =>
  requestDownload({
    defaultFileName: 'File_Level_Organization.doc',
    prefix,
    url: `/StaffProfile/${id}/ExportLevelUpNote`,
    data: {
      id,
      currentLevel,
      nextLevelStartDate,
      note,
    },
    method: 'POST',
  });



